body{
  margin:0;
  padding:0;
  background: rgb(255,0,0);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,0,0,1)), color-stop(48%, rgba(121,9,9,1)), to(rgba(0,0,0,1)));
  background: -webkit-linear-gradient(top, rgba(255,0,0,1) 0%, rgba(121,9,9,1) 48%, rgba(0,0,0,1) 100%);
  background: -o-linear-gradient(top, rgba(255,0,0,1) 0%, rgba(121,9,9,1) 48%, rgba(0,0,0,1) 100%);
  background: linear-gradient(180deg, rgba(255,0,0,1) 0%, rgba(121,9,9,1) 48%, rgba(0,0,0,1) 100%);  background-image: url('./Art-Background-Image.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height:100%;
  font-family: 'Source Sans Pro', sans-serif;
}

.App {
  text-align: center;
  background-color: rgba(255,255,255, 0.95);
  border-radius: 30px;
  margin:20px;
  -webkit-box-shadow: 2px 2px 2px rgba(0,0,0,0.3);
          box-shadow: 2px 2px 2px rgba(0,0,0,0.3);
  min-width: 200px;
  max-width: 400px;
  -ms-flex:1 1;
      flex:1 1;
}
#root{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.time-block{
  border-top:1px solid grey;
  padding:0.2em;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex:1 1;
      flex:1 1;
}
.time-block div:first-child{
  margin-left:15px;
}
.time-block div:nth-child(2){
  margin-right:15px;
}


.time-block-container{
  height:57%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-width: 400px;
  margin:0 auto;
}

h1{
  font-size: 1.3em;
  margin:0;
  padding:10px 30px;
}
h2{
  margin:0;
}



